.market {
	display: grid;
	grid-template-columns: auto 1fr;
	grid-template-rows: auto 1fr;
	gap: 30px;

	.market_top {
		justify-content: space-between;
		grid-area: 1 / 2 / 2 / 3;
	}

	.sidebar {
		grid-area: 1 / 1 / 3 / 2;
	}

	.main {
		grid-area: 2 / 2 / 3 / 3;
	}
}

.button_burger {
	display: none;
}

.market_top {
	position: relative;
	margin-top: 25px;
	padding-bottom: 32px;
	display: flex;

	&::after {
		position: absolute;
		content: '';
		bottom: 0;
		left: 0;
		width: 100%;
		height: 2px;
		background: #45D9FC;
	}

	.top_left {
		display: flex;
		align-items: center;
		gap: 10px;

		button {
			padding: 10px 20px;
		}
	}

	.title {
		font-size: 1.5rem;
		min-width: 195px;
		font-family: 'b Biger Over', system-ui, sans-serif;
		line-height: 1.3;
	}
}

.main {
	.result {
		color: rgba(228, 226, 226, 0.60);
		font-size: 1rem;
		line-height: 1.1;
		letter-spacing: 0.5px;
		margin-bottom: 35px;
	}

	.main_body {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		background: rgba(44, 45, 58, 0.60);
		min-height: 1600px;
		text-align: center;
	}

	.notFound {
		display: inline-flex;
		flex-direction: column;
		align-items: center;
		margin-top: 120px;
		gap: 32px;
		font-family: 'b Biger Over', system-ui, sans-serif;
		line-height: 2;
	}

	.main_list {
		display: grid;
		justify-content: center;
		grid-template-columns: 1fr 1fr;
		gap: 40px;
		padding: 32px 26px;
	}

	.next_robots {
		margin-top: auto;
		display: flex;
		gap: 15px;
		justify-content: center;
		padding: 30px 0 40px 0;

		.next_pages {
			background-color: transparent;
			font-size: 1rem;
			line-height: 1.2;
			color: #fff;
			padding: 3px 8px;
			transition: all .3s ease;

			&:hover {
				color: #272244;
				background: #F25AC9;
			}

			&.active {
				color: #272244;
				background: #F25AC9;
			}
		}
	}
}

@media (max-width: 1240px) {
	.market {
		grid-template-columns: minmax(auto, calc((1vh + 1vw) * 15)) 1fr;
	}

	.main {
		.main_list {
			gap: 24px;
			padding: 28px 20px;
		}
		.main_body {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			background: rgba(44, 45, 58, 0.60);
			min-height: 1200px;
			text-align: center;
		}
	}
}

@media (max-width: 780px) {
	.market {
		display: flex;
		flex-direction: column;
		gap: 20px;
	}

	.sidebar {
		position: fixed;
		top: 0;
		left: -100vw;
		transition: all 0.4s ease;
		z-index: 100;

		&.active {
			background: linear-gradient(#063652 25%, #000f17 100%);
			border: 1px solid #063652;
			width: 60%;
			max-width: 380px;
			height: 100%;
			padding: 50px 25px 50px 25px;
			left: 00px;
			overflow-y: scroll;
			top: 0;
		}
	}

	.button_burger {
		display: block;
		margin-left: 15px;
	}

	.market_top {
		margin-top: 10px;
		padding-bottom: 20px;

		.top_left {
			display: flex;
			align-items: center;
			gap: 7px;

			button {
				padding: 10px 30px;
			}
		}

		.title {
			font-size: 1.1rem;
			min-width: 140px;
		}
	}

	.main {
		.notFound {
			margin-top: 50px;
			gap: 20px;
		}
	}

	.main {
		.main_list {
			grid-template-columns: 1fr 1fr;
			gap: 20px;
			padding: 20px;
		}
	}
}

@media (max-width: 550px) {
	.main {
		text-align: center;

		.main_list {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
		}
	}
}

@media (max-width: 480px) {
	.market {
		gap: 10px;
	}

	.sidebar {
		&.active {
			width:	75%;
			padding: 40px 20px 0 20px;
			left: 00px;
			top: 0;
		}
	}

	.market_top {
		padding-bottom: 16px;

		.top_left {
			gap: 5px;

			button {
				max-height: 32px;
				max-width: 70px;
				padding: 0px 5px;
				font-size: 0.9rem;
			}
		}

		.title {
			font-size: 0.9rem;
			min-width: 0;
		}
	}

	.main {
		padding: 0;

		.result {
			margin: 15px 0;
		}

		.notFound {
			margin-top: 50px;
			gap: 20px;

			img {
				width: 250px;
				height: auto;
			}
		}

		.main_list {
			gap: 24px;
			padding: 20px;
		}
	}
}